<template>
  <div>
    <div>
      <el-link
        :underline="false"
        type="primary"
        style="
          font-size: 16px;
          cursor: pointer;
          float: right;
          margin-bottom: 10px;
          color: ;
        "
        @click="add"
        ><i class="iconfont iconxinjian" style="padding-right: 5px"></i
        >新增</el-link
      >
    </div>

    <el-table
      v-loading="loading"
      ref="multipleTable"
      :data="tableData"
      size="small"
      border
      :height="height - 60"
      style="
        width: 100%;
        text-align: center;
        line-height: 18px;
        overflow-y: auto;
      "
    >
      <!-- <el-table-column
          type="selection"
          width="55"
          align="center"
        ></el-table-column> -->
      <el-table-column
        label="社群名"
        align="center"
        prop="name"
        show-overflow-tooltip
      ></el-table-column>

      <el-table-column
        label="当前人数"
        align="center"
        width="100"
        prop="persons"
      >
      </el-table-column>

      <el-table-column label="是否启用" align="center" prop="status">
        <template slot-scope="{ row }">
          {{ row.status ? "是" : "否" }}
        </template>
      </el-table-column>

      <el-table-column
        label="群二维码"
        align="center"
        prop="qrcode"
      >
      <template slot-scope="{ row }">
          <el-image
            style="width: 100px; height: 100px"
            :src="row.qrcode"
            fit="cover"
          ></el-image>
        </template>
    </el-table-column>

      <el-table-column label="操作" align="center" width="140">
        <template slot-scope="{ row }">
          <i
            class="el-icon-edit"
            style="margin-right: 20px"
            @click="handleEdit(row)"
          ></i>
          <i
            class="el-icon-delete"
            style="margin-right: 0"
            @click="handleDelete(row)"
          ></i>
        </template>
      </el-table-column>
      <template slot="empty">
        <div>
          <i
            class="iconfont iconzanwuziyuan"
            style="font-size: 40px; color: #8c8c8c"
          >
            <p style="font-size: 14px; margin-top: -30px">暂无资源</p>
          </i>
        </div>
      </template>
    </el-table>

    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogFormVisible"
      width="650px"
      :close-on-click-modal="false"
      :append-to-body="true"
      @closed="resetForm"
    >
      <el-form :model="form" :rules="rules" ref="ruleForm" size="mini">
        <el-form-item label="社群名:" prop="name">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="二维码:" prop="qrcode">
          <upload @success="handleSuccess" ref="myupload" :url="url"></upload>
        </el-form-item>
        <el-form-item label="是否启用:">
          <el-switch
            v-model="form.status"
            active-color="#13ce66"
            inactive-color="#ccc"
          >
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelForm" size="mini">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')" size="mini"
          >提 交</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import upload from "@/pages/common/upload.vue";
export default {
  name: "community_cop",
  components: {
    upload
  },
  props: {
    height: {
      type: Number,
    },
  },
  data() {
    return {
      loading: false,
      tableData: [],
      dialogFormVisible: false,
      dialogTitle: "管理",
      form: {
        name: "",
        qrcode: "",
        status:false
      },
      rules: {
        name: [
          { required: true, message: "请输入名称", trigger: "blur" },
          {
            min: 1,
            max: 24,
            message: "长度在 1 到 24 个字符",
            trigger: "blur",
          },
        ],
      },
      url:null
    };
  },
  created() {},
  mounted() {
    this.getData()
  },
  methods: {
    getData(){
    this.$reqGet("/group/data").then((res) => {
        if (res.code == 200) {
          this.tableData = res.data;
        } else {
          this.$message.error(res.msg);
        }
      });
   },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let url = this.form.id
            ? "/group/edit"
            : "/group/add";
          this.$reqPost(url, {
            ...this.form,
          }).then((res) => {
            if (res.code == 200) {
              this.dialogFormVisible=false
              this.getData()
            } else {
              this.$message.error(res.msg);
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    cancelForm() {
      this.dialogFormVisible = false;
    },
    resetForm() {
      this.$refs["ruleForm"].resetFields();
      this.form.name=""
      this.form.qrcode=null
      this.form.status=false
      this.url=null
      this.$refs.myupload.handleRemove();
    
    },

    add() { 
      this.dialogFormVisible = true;
    },
    handleEdit(row) {
     
      this.form = {
        ...row,
      };
      this.url = row.qrcode;
      this.dialogFormVisible = true;
    
    },

    handleDelete(row) {
      this.$confirm("确定删除?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        closeOnClickModal: false,
      }).then(() => {
        console.log(row);
      });
    },
    handleSuccess(res) {
      console.log(res);
      this.form.qrcode= res.url;
    },
 
  },
};
</script>
<style scoped>
</style>
