<template>
  <el-upload
    class="avatar-uploader"
    :show-file-list="false"
    :limit="1"
    action=""
    :headers="{ 'content-type': 'application/json' }"
    :data="{ url: imageUrl }"
    name="file"
    :on-success="handleAvatarSuccess"
    :before-upload="beforeAvatarUpload"
    :before-remove="handleRemove"
  >
    <img v-if="imageUrl" :src="imageUrl" class="avatar" />

    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
  </el-upload>
</template>

<script>
import Myoss from '@/utils/oss/oss';
import { v4 as uuidv4 } from "uuid";
export default {
  name: "upload-com",
  props: {
    height: {
      type: Number,
    },
    url:{
        type:String
    }
   
  },
  watch:{
    url(val){
      
        if(val!=''&&val!=null)this.imageUrl=val
    }
  },
  data() {
    return {
      imageUrl: "",
    };
  },
  created() {
    this.imageUrl=this.url
  },
  methods: {
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isPng = file.type === "image/png";
      const isLt5M = file.size / 1024 / 1024 < 5;

      if (!isJPG && !isPng) {
        this.$message.error("上传图片只能是 JPG或者PNG 格式!");
      } else if (!isLt5M) {
        this.$message.error("上传图片大小不能超过 5MB!");
      } else {
        let uuid=uuidv4()
        let time=new Date().getTime().toString().split("").reverse().join("");
        let type=file.type.split('/')[1]
        let name=`${uuid}-${time}.${type}`
        console.log(name)
        Myoss.putFile(name, file)
          .then((res) => {
            this.$emit('success',res.data)
          })
          .catch(function (err) {
            this.$emit('fail',err)
          });
      }
      return (isJPG || isPng) && isLt5M;
    },
    setImage(url){
        this.imageUrl=url
    },
    handleRemove() {
      this.imageUrl = "";
    },
  },
};
</script>

<style scoped>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
  border: 1px dotted #8c939d;
  border-radius: 5px;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
  border: 1px dotted #8c939d;
  border-radius: 5px;
}
</style>
